import createVnode from "./utils/createVnode";

const SmpReact = {
  createElement(tag) {
    const {elementName: type, attributes: data, children} = tag;
    const {key, ref, ...props} = data;
    console.log(props);

    // Â§ÑÁêÜÊñáÊú¨ÂÜÖÂÆπ
    let text;

    // Â§ÑÁêÜÂ≠êÁ∫ßÂàóË°®‰∏≠ÁöÑ string or number
    // ÂêåÊ†∑ËΩ¨Êç¢‰∏∫ VNode
    if (children && children.length) {
      let i, l = children.length;
      for (i = 0; i < l; ++i) {
        const child = children[i];
        if (['string', 'number'].includes(typeof child)) {
          if (type === 'Text') {
            // Âü∫‰∫é WebGL ÁöÑÈúÄË¶ÅÔºåÂå∫Âà´‰∫é DOM
            // ËøôÈáåÊñ∞Â¢û‰∏Ä‰∏™ <Text> Ê†áÁ≠æÔºåvnode.type === 'Text'
            // ÈúÄÁâπÊÆäÂ§ÑÁêÜ
            if (text === undefined) text = '';
            text += String(child);
          } else {
            // ÈùûÊ†áÁ≠æÁöÑÊñáÂ≠óËäÇÁÇπÔºå vnode.type === undefined
            // ‰æãÂ¶Ç <Container>Text</Container>
            // ‰∏≠Èó¥ÁöÑ Text ÂÖ∂ÂÆûÊòØÂêåÊ†∑ÈúÄË¶Å‰∏Ä‰∏™ÊñáÂ≠óÂÖÉÁ¥†
            children[i] = createVnode(
              undefined,
              {},
              undefined,
              undefined,
              undefined,
              undefined,
              String(children[i])
            );
          }
        }
      }
    }
    return createVnode(type, props, ref, key, children, undefined, text);
  }
};



